<template>

  <div class="page-container" style="background-color: white" >
	<!-- top tool bar -->
	<div class="toolbar" style="padding-top:20px;padding-left:15px;" >



    <el-card class="box-card">
    <div slot="header" class="clearfix">
        <span>{{$t('panel.panelSysInfo.systemStoreInfo')}}</span>
        <el-button style="float: right; padding: 3px 0" type="text"></el-button>
    </div>

        <!-- https://www.npmjs.com/package/vue2-ace-editor -->
        <editor id="code" v-model="content" @init="editorInit" lang="sh" theme="terminal" width="auto" height="250" ></editor>
    </el-card>

	</div>
  </div>
</template>

<script>
export default {
    components:{
        editor: require('vue2-ace-editor')
    },
    mounted: async function() {
        let loginInfo = this.$store.state.loginInfo.loginInfo;
        let params = {
            hostIp: loginInfo.data.hostIp,
            username: loginInfo.data.username,
            path: ''
        };

        let ret = await this.$api.optOsInfo.getDf(params);
        let total = '';
        for(let i = 0; i < ret.data.length; i++) {
            total += ret.data[i]+'\n';
        }
        this.content = total;
    },
    data() {
        return {
            content:'',

        };
    },
    methods: {
        editorInit: function () {
            require('brace/ext/language_tools') //language extension prerequsite...
            require('brace/mode/sh')                
            require('brace/mode/sh')    //language
            require('brace/mode/less')
            require('brace/theme/terminal')
            require('brace/snippets/sh') //snippet

            let editor = ace.edit("code");
            // font size
            editor.setFontSize(14);
            // readonly setting
            editor.setReadOnly(true);
            // 自动换行,设置为off关闭
            editor.setOption("wrap", "free")
        }
    },
}
</script>




