<template>
  <!-- Copy Form -->
  <el-dialog :visible.sync="dialogCopyFormVisible" :showClose="false" :beforeClose="closeDialog" :width="'30%'">
    <el-tag>{{tableDataItem.path}}
      <!-- <el-tag size="mini" type="success"> {{tableDataItem.path}} </el-tag> -->
    </el-tag>
    &nbsp;
    <el-badge class="item">{{$t('panel.panelAllFiles.dialogCopyDeal.copyTo')}} </el-badge>
    &nbsp;
    <el-tag>{{this.targetPath}}</el-tag>
    <el-form>

      <!-- <el-form-item  label="序号">
        <el-input v-model="tableDataItem.index" autocomplete="off" :disabled="true"></el-input>
      </el-form-item> -->

    <el-tree
      :data="dialogCopyTreeData"
      :props="dialogCopyTreeDefaultProps"
      accordion
      @node-click="pointDialogCopyHandleNodeClick">
    </el-tree>

    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="pointDialogCopyEditOver(false)">{{$t('common.cancel')}}</el-button>
      <el-button type="primary" @click="pointDialogCopyEditOver(true)">{{$t('common.confirm')}}</el-button>
    </div>

  </el-dialog>
</template>

<script>
export default {
    name: 'DialogCopyDeal',
    props: {
        tableDataItem: {
            type: Object,
            required: true,
        },
        dialogCopyFormVisible: {
            type: Boolean,
            required: true,
        },
        location: {
            type: String,
            required: true,
        },
    },
    created: function() {
        this.targetPath = this.location;
    },
    data(){
        return {            
            dialogCopyTreeData: [{
                label: '/',
                children: []
            }],
            dialogCopyTreeDefaultProps: {
                children: 'children',
                label: 'label'
            },
            targetPath: '/'
        };
    },
    methods: {
        closeDialog: function(){
            this.$emit('showDialogCopyDeal', false);
            this.$emit('flushTableData', this.location);
        },
        pointDialogCopyHandleNodeClick: function(data) {
            // this.$api.output.log('pointDialogCopyHandleNodeClick 1 ', data.label);
            let returnedItem;
            let loginInfo = this.$store.state.loginInfo.loginInfo;
            let thisApi = this.$api;
            let self = this;
            let find = function(arr, label){
                 arr.forEach((item)=>{ //利用foreach循环遍历
                    if(item.label === label)//判断递归结束条件
                    {        
                        returnedItem = item;
                        // this.$api.output.log('last out: ', item.label);
                        // this.$api.output.log('last out: ', item.children);
                        
                        let children = [];
                        
                        let argListFiles = {
                            hostIp: loginInfo.data.hostIp,
                            username: loginInfo.data.username,
                            location: item.label
                        };

                        thisApi.optFiles.listFiles(argListFiles).then((rets)=>{
                            for(let i = 0; i < rets.data.length; i++) {
                                if (rets.data[i].type !== 'dir') continue;
                                // console.log('rets[', i,'] = ', rets.data[i]);
                                let node = {'label': rets.data[i].path, 'children':[]};
                                children.push(node);
                            }
                            // console.log('last out children: ', children);
                            self.targetPath = item.label;
                            item.children = children;
                        })
                        return item;
                    }
                    else if(item.children.length > 0) //判断chlidren是否有数据
                    {
                        // console.log('out: ', label);
                        find(item.children, label);  //递归调用                      
                    }                   
                })
            }

            let item = find(this.dialogCopyTreeData, data.label);
            // console.log("pointDialogCopyHandleNodeClick item:", item);


            // let loginInfo = this.$store.state.loginInfo.loginInfo;
            // let argListFiles = {
            //     hostIp: loginInfo.data.hostIp,
            //     username: loginInfo.data.username,
            //     location: data.label
            // };
            // let rets = await this.$api.optFiles.listFiles(argListFiles);
            // console.log('pointDialogCopyHandleNodeClick 2 ', rets);

            // let oldPointNode = this.dialogCopyTreeData[0];
            // let pointNode = this.getNode(oldPointNode, data.label);
            // console.log('pointDialogCopyHandleNodeClick pointNode ', pointNode.label);
            
            // for(let i = 0; i < rets.data.length; i++) {
            //     if (rets.data[i].type !== 'dir') continue;
            //     // console.log('rets[', i,'] = ', rets.data[i]);
            //     let node = {'label': rets.data[i].path, 'children':[]};
            //     pointNode.children.push(node);
            // }
            // console.log('pointDialogCopyHandleNodeClick 3 ', pointNode);
        },
        pointDialogCopyEditOver: async function(flag){
            
            if(flag) {
                
                let loginInfo = this.$store.state.loginInfo.loginInfo;

                let arg = {
                    hostIp: loginInfo.data.hostIp,
                    username: loginInfo.data.username,
                    oldPath: this.tableDataItem.path,
                    newPath: this.targetPath+'/'+this.tableDataItem.name
                };

                let ret = await this.$api.optFiles.copyFile(arg);
            }

            let ppos = this.tableDataItem.path.lastIndexOf('/');
            let path = '/';
            if (ppos !== 0) {
                path = this.tableDataItem.path.substring(0, ppos);
                path += '/'
            }

            this.$emit('flushTableData', path);
            this.$emit('showDialogCopyDeal', false);
            
        },
    }
}
</script>

<style>

</style>
  


