import axiosClient from '../axios-client'
import Vue from 'vue'
import axios from 'axios';

// list all files or floders
export const listFiles = (params) => {
    return axiosClient({
        url: '/listFiles',
        method: 'post',
        data: params
    })
}

export const renameFile = (params) => {
    return axiosClient({
        url: '/rename',
        method: 'post',
        data: params
    })
}

export const shareFile = (params) => {
    return axiosClient({
        url: '/getShare',
        method: 'post',
        data: params
    })
}

export const removeFile = (params) => {
    return axiosClient({
        url: '/remove',
        method: 'post',
        data: params
    })
}

export const copyFile = (params) => {
    return axiosClient({
        url: '/copy',
        method: 'post',
        data: params
    })
}

export const mkdir = (params) => {
    return axiosClient({
        url: '/mkdir',
        method: 'post',
        data: params
    })
}

export const downloadFile = (params, downloadFileName) => {
    let baseUrl = Vue.prototype.baseConfig.baseUrl;
    let url = baseUrl+'/getFile'
    let data = params;
    const Fs = require('fs')

    console.log('opt-files.js - downloadFile1:', data);
    axios({
        url: url,
        method: 'post',
        data: data,
        responseType: 'blob',
    }).then(
      function (response) {
        data = response.data;
  　　　 // 创建a标签并点击， 即触发下载
        let blob = new Blob([data], {type: data.type})
        console.log('opt-files.js - downloadFile2:', data.type);
        let downloadElement = document.createElement('a')
        let href = window.URL.createObjectURL(blob); //创建下载的链接
        downloadElement.href = href;
        downloadElement.download = downloadFileName; //下载后文件名
        document.body.appendChild(downloadElement);
        downloadElement.click(); //点击下载
        document.body.removeChild(downloadElement); //下载完成移除元素
        window.URL.revokeObjectURL(href); //释放blob对象
        console.log('opt-files.js - downloadFile2: download ok');

        axios({
            url: baseUrl+'/getFileAfter',
            method: 'post',
            data: {fileName: downloadFileName}
        }).then(()=>{

        });
      }

    ).catch(function (err) {
      console.log(err)
    })


}


