import navMenu from './nav-menu'

export default {
    state: {
        // editableTabsValue: 'test1', //name
        // editableTabs: [
        //     {title: '测试面板1', name: 'test1', content: '测试'},
        //     // {title: 'test2', name: 'test2', content: 'Tab 2 content'},
        //     // {title: 'test3', name: 'test3', content: 'Tab 3 content'},
        //     // {title: 'test4', name: 'test4', content: 'Tab 4 content'},
        // ]
        editableTabsValue: navMenu.state.initTab.editableTabsValue,
        editableTabs: navMenu.state.initTab.editableTabs,
    },
    getters: {
    },
    mutations: {
        setEditableTabsValue(state, editableTabsValue){
            state.editableTabsValue = editableTabsValue;
        },
        pushEditableTabs(state, editableTabsItem){
            let isExistedTabElem = false;
            let tabItem = '';
            for(let i = 0; i < state.editableTabs.length; i++) {
                if(state.editableTabs[i].name === editableTabsItem.name) {
                    tabItem = state.editableTabs[i];
                    isExistedTabElem = true;
                    break;
                }
            }
            // console.log('tabItem = ', tabItem);
            if (isExistedTabElem === true) {
                state.editableTabsValue = tabItem.name;
                // this.$router.push(state.editableTabsValue);
            } else {
                state.editableTabs.push({
                    title: editableTabsItem.title,
                    name: editableTabsItem.name,
                    content: editableTabsItem.content
                });
                state.editableTabsValue = editableTabsItem.name;
                // this.$router.push(state.editableTabsValue);
            }
        },
        popEditableTabs(state, editableTabsValue) {
            let isExistedTabElem = false;
            let tabItem = null;
            for(let i = 0; i < state.editableTabs.length; i++) {
                if(state.editableTabs[i].name === editableTabsValue) {
                    state.editableTabs.splice(i, 1);
                    if (i>0) {
                        tabItem = state.editableTabs[i-1];
                    } else {
                        tabItem = null;
                    }
                    
                    isExistedTabElem = true;
                    break;
                }
            }
            // console.log('tabItem = ', tabItem);
            if (isExistedTabElem === true) {
                if (tabItem === null) {
                    state.editableTabsValue = '/';
                } else {
                    state.editableTabsValue = tabItem.name;
                }
                
            } 
        }
    },
    actions: {
    }
}


