import axiosClient from '../axios-client'

// 
export const getHistory = (params) => {
    return axiosClient({
        url: '/getHistory',
        method: 'post',
        data: params
    })
}


export const getDf = (params) => {
    return axiosClient({
        url: '/getDf',
        method: 'post',
        data: params
    })
}


