import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login'
import Home from '@/views/Home'
import P404 from '@/views/404'
import store from '@/store'

import PanelAllFiles from '@/views/Content/PanelAllFiles'
import PanelShareFiles from '@/views/Content/PanelShareFiles'
import PanelHistory from '@/views/Content/PanelHistory'
import PanelSysInfo from '@/views/Content/PanelSysInfo'
import Test2 from '@/views/Content/Test2'
import Test3 from '@/views/Content/Test3'
import Test4 from '@/views/Content/Test4'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {      
          path: '/panelAllFiles',
          name: 'PanelAllFiles',
          component: PanelAllFiles
        },
        {      
          path: '/panelShareFiles',
          name: 'PanelShareFiles',
          component: PanelShareFiles
        },
        {      
          path: '/panelHistory',
          name: 'PanelHistory',
          component: PanelHistory
        },
        {      
          path: '/panelSysInfo',
          name: 'PanelSysInfo',
          component: PanelSysInfo
        },
      ]
    },
    {
      path: '/404',
      name: '404',
      component: P404
    },
  ]
})





