<template>
  <div class="page-container" style="background-color: white" :onloaded="onloaded()">
	<!-- top tool bar -->
	<div class="toolbar" style="float:left;padding-top:10px;padding-left:15px;" >
    &nbsp;
    <el-button type="primary" @click="showUploadFileDialog(true)">{{$t('panel.panelAllFiles.base.upload')}}</el-button>
    <el-button type="primary" plain @click="pointGoParent()">{{$t('panel.panelAllFiles.base.goParent')}}</el-button>
    <el-button @click="showMkdirDialog()">{{$t('panel.panelAllFiles.base.newFolder')}}</el-button>
	</div>

  <div style="padding-top:60px;padding-left:15px;">
    
  <!-- 分割线 -->
  <el-divider></el-divider>

  <el-table ref="multipleTable" :data="pageLists" tooltip-effect="dark" style="width: 100%" :row-style="rowStyle" :cell-style="cellStyle" @selection-change="handleSelectionChange" :row-class-name="tableRowClassName">
    <!-- <el-table-column type="selection" width="55"></el-table-column> -->
    <!-- <el-table-column type="index" label="序号" width="50"></el-table-column> -->
    <el-table-column :label="$t('panel.panelAllFiles.base.type')" width="120" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-button v-if="tableData[scope.row.index].type === 'dir'" type="text" size="small"  @click="pointGoChild(scope.row.index, tableData)">
          <i class="fa fa-folder fa-2x" aria-hidden="true"></i>
          </el-button>
          <!-- <el-button v-else type="text" size="small"  @click="handleFileDownloadDeal(scope.row.index, tableData)">
          <i class="fa fa-file-o fa-2x" aria-hidden="true"></i>
          <i class="fa fa-download fa-2x" aria-hidden="true"></i>
          </el-button> -->

          <el-button v-else type="text" size="small">
            <i class="fa fa-file-o fa-2x" aria-hidden="true"></i>
          </el-button>

        </template>
    </el-table-column>
    <!-- <el-table-column v-else prop="type" label="类型" width="120" show-overflow-tooltip>
      <i class="fa fa-file-o" aria-hidden="true"></i>
    </el-table-column> -->
 
    <el-table-column prop="name" :label="$t('panel.panelAllFiles.base.name')" width="200" show-overflow-tooltip></el-table-column>
    <el-table-column prop="size" :label="$t('panel.panelAllFiles.base.size')" width="120" show-overflow-tooltip></el-table-column>
    <el-table-column prop="mTime" :label="$t('panel.panelAllFiles.base.mTime')" width="120" show-overflow-tooltip></el-table-column>
    <el-table-column prop="path" :label="$t('panel.panelAllFiles.base.path')" show-overflow-tooltip></el-table-column>
<el-table-column
      fixed="right"
      :label="$t('panel.panelAllFiles.base.operation')"
      width="200">
      <template slot-scope="scope">

        <!-- move -->
        <el-button type="text" size="small"  @click="showDialogMoveDealEx(scope.row.index, tableData)">
          <i class="fa fa-arrows fa-2x" aria-hidden="true"></i>
        </el-button>
      
        <el-popover placement="top" width="100" :ref="scope.$index">
          <!-- <div style="text-align: right; margin: 0"> -->
            <div style="margin: 1">
            
            <!-- download -->
            <el-button v-if="tableData[scope.row.index].type === 'file'" type="text" size="small"  @click="handleFileDownloadDeal(scope.row.index, tableData)">
            <i class="fa fa-download fa-2x" aria-hidden="true"></i>
            </el-button>

            <!-- copy -->
            <el-button type="text" size="mini"  @click="showDialogCopyDealEx(scope.row.index, tableData)">
              <i class="fa fa-clipboard fa-2x" aria-hidden="true"></i>
            </el-button>

            <!-- rename -->
            <el-button type="text" size="mini"  @click="showDialogRenameDealEx(scope.row.index, tableData)">
              <i class="fa fa-pencil-square-o fa-2x" aria-hidden="true"></i>
            </el-button>

            <!-- share -->
            <el-button v-if="tableData[scope.row.index].type === 'file'" type="text" size="mini"  @click="handleShareDeal(scope.row.index, tableData)">
                <i class="fa fa-share-alt fa-2x" aria-hidden="true"></i>
            </el-button>

            <!-- temp save -->
            <!-- <el-button v-if="tableData[scope.row.index].type === 'file'" type="text" size="mini"  @click="handleTempSaveDeal(scope.row.index, tableData)">
                <i class="fa fa-archive fa-2x" aria-hidden="true"></i>
            </el-button> -->

            <!-- delete -->
            <el-button type="text" size="mini"  @click="handleDeleteDeal(scope.row.index, tableData)">
                <i class="fa fa-trash-o fa-2x" aria-hidden="true"></i>
            </el-button>
          </div>

            <p></p>
            <!-- more button -->
            <el-button type="text" size="small" slot="reference">
              <i class="fa fa-th-large fa-2x" aria-hidden="true"></i>
            </el-button>
            
        </el-popover>
        

      </template>
    </el-table-column>
  </el-table>

  <p></p>
  <el-divider></el-divider>
  <p></p>
  <div style="float:left">
    <el-pagination background layout="prev, pager, next"
    @current-change="handleCurrentChange"
    :current-page="currentPage"
    :page-size="10"
    :total="listsDataNumber" 
    ></el-pagination>
  </div>
  </div>

  <DialogRenameDeal 
  :tableDataItem="tableDataItem" 
  :location="location" 
  :dialogRenameFormVisible="dialogRenameFormVisible"
  @flushTableData="flushTableData" 
  @showDialogRenameDeal="showDialogRenameDeal"></DialogRenameDeal>

  <DialogMoveDeal 
  :tableDataItem="tableDataItem" 
  :location="location" 
  :dialogMoveFormVisible="dialogMoveFormVisible"
  @flushTableData="flushTableData"
  @showDialogMoveDeal="showDialogMoveDeal"
  ></DialogMoveDeal>


  <DialogCopyDeal 
  :tableDataItem="tableDataItem" 
  :location="location" 
  :dialogCopyFormVisible="dialogCopyFormVisible" 
  @flushTableData="flushTableData" 
  @showDialogCopyDeal="showDialogCopyDeal" 
  ></DialogCopyDeal>


  <DialogFileUploadDeal 
  :dialogFileUploadFormVisible="dialogFileUploadFormVisible" 
  :location="location" 
  @showUploadFileDialog="showUploadFileDialog"
  @flushTableData="flushTableData"></DialogFileUploadDeal>



  </div>

</template>

<script>
import axios from 'axios';
import Vue from 'vue'
import DialogMoveDeal from './PanelSubContent/DialogMoveDeal'
import DialogFileUploadDeal from './PanelSubContent/DialogFileUploadDeal'
import DialogRenameDeal from './PanelSubContent/DialogRenameDeal'
import DialogCopyDeal from './PanelSubContent/DialogCopyDeal'
export default {
    components: {
        DialogMoveDeal,
        DialogFileUploadDeal,
        DialogRenameDeal,
        DialogCopyDeal,
        // DialogMoreDeal
    },
    created: function() {
        this.getLocation();
    },
    data() {
      return {
        loading: null,
        restaurants: [],
        state: '',
        timeout:  null,
        tableData: null,
        multipleSelection: [],

        currentPage: 1,
        listsDataNumber: 0,
        pageLists: [],

        dialogRenameFormVisible: false,
        tableDataItem: {
          index: '',
          name: '',
          type: '',
          size: '',
          mTime: '',
          path:''
        },
        
        dialogMoveFormVisible: false,
        dialogCopyFormVisible: false,
        dialogFileUploadFormVisible: false,
        dialogMoreFormVisible: false,
        location: '/home',
      };
    },
    methods: {
      // go parent dir
      pointGoParent: async function(){
        let pos = this.location.lastIndexOf('/');
        let path = '/';
        if (pos !== 0) {
          path = this.location.substring(0, pos);
        }
        this.location = path;
        this.flushTableData(path);
      },
      // go child folder
      pointGoChild: async function(index, rows) {
          let path = rows[index].path;
          this.location = path;
          this.flushTableData(path);
      },

      // upload
      showUploadFileDialog: function(flag) {
          this.dialogFileUploadFormVisible = flag;     
      },
      // download
      handleFileDownloadDeal: async function(index, rows) {
        let path = rows[index].path;
        let loginInfo = this.$store.state.loginInfo.loginInfo;
        let data = {
            hostIp: loginInfo.data.hostIp,
            username: loginInfo.data.username,
            remotePath: path
        };
        this.$api.optFiles.downloadFile(data, rows[index].name);
      },

      showMkdirDialog: function(){
        this.$prompt(this.$t('panel.panelAllFiles.base.newFolderTip1'), this.$t('common.tip'), {
                  confirmButtonText: this.$t('common.confirm'),
                  cancelButtonText: this.$t('common.cancel'),
                  // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*/,
                  inputPattern: /.*/,
                  inputErrorMessage: this.$t('panel.panelAllFiles.base.newFolderTip2')
                }).then(({ value }) => {
                  this.$message({
                    type: 'success',
                    message: this.$t('panel.panelAllFiles.base.newFolderTip3') + value
                  });

                  let loginInfo = this.$store.state.loginInfo.loginInfo;
      
                  let argMkdir = {
                      hostIp: loginInfo.data.hostIp,
                      username: loginInfo.data.username,
                      path: this.location+'/'+value,
                  };

                  this.$api.optFiles.mkdir(argMkdir).then(()=>{
                      var sleepDeal = function(time) {
                      var startTime = new Date().getTime() + parseInt(time, 10);
                          while(new Date().getTime() < startTime) {}
                      };
                      sleepDeal(1000); // 延时函数，单位ms
                      this.flushTableData(this.location);
                  }).catch(()=>{});

                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: this.$t('common.cancelInput')
                  });       
        });
      },
      showDialogMoveDealEx: function(index, rows){
        this.$api.output.log('showDialogMoveDealEx = ', rows[index]);
        this.tableDataItem.index = index;
        this.tableDataItem.path = rows[index].path;
        this.tableDataItem.name = rows[index].name;
        this.dialogMoveFormVisible = true;
      },
      showDialogMoveDeal: function(flag){
        this.dialogMoveFormVisible = flag;
      },
      showDialogRenameDealEx: function(index, rows){
        this.$api.output.log('showDialogRenameDealEx = ', rows[index]);
        this.tableDataItem.index = index;
        this.tableDataItem.date = rows[index].date;
        this.tableDataItem.name = rows[index].name;
        this.tableDataItem.oldName = rows[index].name;
        this.tableDataItem.path = rows[index].path;
        this.showDialogRenameDeal(true);
      },
      showDialogRenameDeal: function(flag){
        this.dialogRenameFormVisible = flag;
      },
      showDialogCopyDealEx: function(index, rows){
        this.tableDataItem.index = index;
        this.tableDataItem.path = rows[index].path;
        this.tableDataItem.name = rows[index].name;
        this.showDialogCopyDeal(true);
      },
      showDialogCopyDeal: function(flag){
        this.dialogCopyFormVisible = flag;
      },
      handleShareDeal: function(index, rows){
        this.$confirm(this.$t('panel.panelAllFiles.base.shareTip'), this.$t('common.tip'), {confirmButtonText: this.$t('common.confirm'), cancelButtonText: this.$t('common.cancel'), type: 'warning'})
        .then((res)=>{
            let loginInfo = this.$store.state.loginInfo.loginInfo;
            let args = {
            hostIp: loginInfo.data.hostIp,
            username: loginInfo.data.username,
            remotePath: rows[index].path
          };

          this.$api.optFiles.shareFile(args);
        })
        .catch();
      },
      // handleTempSaveDeal: function(index, rows){
      //   this.$confirm('确定执行移到暂存操作？', this.$t('common.tip'), {confirmButtonText: this.$t('common.confirm'), cancelButtonText: this.$t('common.cancel'), type: 'warning'})
      //   .then()
      //   .catch();
      // },
      handleDeleteDeal: function(index, rows){
        this.$confirm(this.$t('panel.panelAllFiles.base.deleteTip'), this.$t('common.tip'), {confirmButtonText: this.$t('common.confirm'), cancelButtonText: this.$t('common.cancel'), type: 'warning'})
        .then(()=>{
            // this.$api.output.log('PanelAllFiles.vue - handleDeleteDeal: confirm');
            let loginInfo = this.$store.state.loginInfo.loginInfo;
      
            let argRemove = {
                hostIp: loginInfo.data.hostIp,
                username: loginInfo.data.username,
                path: rows[index].path,
            };
            // this.$api.output.log('PanelAllFiles.vue - handleDeleteDeal:', argRemove);
            this.$api.optFiles.removeFile(argRemove).then(()=>{
                

                var sleepDeal = function(time) {
                var startTime = new Date().getTime() + parseInt(time, 10);
                    while(new Date().getTime() < startTime) {}
                };
                sleepDeal(1000); // 延时函数，单位ms
                this.flushTableData(this.location);
                
            }).catch(()=>{});

        })
        .catch(()=>{
            // this.$api.output.log('PanelAllFiles.vue - handleDeleteDeal: cancel');
        });

      },

      ///////////////////////////////////////////////////////////
      // main table deal
      rowStyle({row, column, rowIndex, columnIndex}){
        return "height:0";
      },
      cellStyle({row, column, rowIndex, columnIndex}){
        return "padding:0";
      },
      getLocation: function() {
        try{
          let loginInfo = this.$store.state.loginInfo.loginInfo;
          if (loginInfo.data.username === 'root') {
              this.location = '/root';
          } else {
              this.location = '/home/' + loginInfo.data.username;
          }
        } catch(e){
          return;
        }
      },
      loadAllData: async function(){
        
        try{
            this.load();

            let loginInfo = this.$store.state.loginInfo.loginInfo;
            let args = {
              hostIp: loginInfo.data.hostIp,
              username: loginInfo.data.username,
              location: this.location
            };

            let ret = await this.$api.optFiles.listFiles(args);
            this.tableData = ret['data'];
            for(let i = 0; i < this.tableData.length; i++){
              this.tableData[i].index = i; 
              this.tableData[i].size = this.sizeDeal(this.tableData[i].size);
              // this.tableData[i].type = '<i class="fa fa-folder-o" aria-hidden="true"></i>';
            }

        } catch (e){
          // console.log(e);
          return;
        }
        

        this.listsDataNumber = this.tableData.length;
        this.initPageLists()

        // this.$api.output.log('this.pageLists = ', this.pageLists);
        // this.$api.output.log("load ok! ");
      },
      load: function() {
        this.loading = this.$loading({  
          lock: true,
          text: "loading...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.5)",
          fullscreen: false,
          target: document.querySelector("#main-container ")
        })
      },
      onloaded: function() {
        // console.log('onload ...');
        if(this.loading) {
          this.loading.close();
        } 
      },
      tableRowClassName: function({row, rowIndex}) {
        if (rowIndex % 4 === 0) {
          return 'warning-row';
        } else if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },
      handleSelectionChange: function(val) {
        this.multipleSelection = val;
      },
      handleCurrentChange(val) {
          this.pageLists = this.tableData.slice((val-1)*10,val*10)
      },
      initPageLists(){
          this.pageLists = this.tableData.slice(0,10)
      },

      flushTableData: async function(path) {
          
          this.$api.output.log('PanelAllFiles.vue - flushTableData',path);

          let loginInfo = this.$store.state.loginInfo.loginInfo;
          let argListFiles = {
            hostIp: loginInfo.data.hostIp,
            username: loginInfo.data.username,
            location: path
          };

          let ret = await this.$api.optFiles.listFiles(argListFiles);
          // console.log('PanelAllFiles.vue - pointGoParent2', ret);
          this.tableData = ret['data'];
          for(let i = 0; i < this.tableData.length; i++){
            this.tableData[i].index = i; 
            this.tableData[i].size = this.sizeDeal(this.tableData[i].size);
          }

          this.listsDataNumber = this.tableData.length;
          this.initPageLists()
      },

      // file size unit trans
      sizeDeal: function(size) {
        let bLen = size.toString().length;
        if (bLen < 4){
           return size.toString()+' B';
        }

        if (bLen >= 4 && bLen <= 7){
          let s = size/1024;
          s = Math.round(s);
          return s.toString()+' KB';
        }

        if (bLen >= 8){
          let s = size/1024/1024;
          s = Math.round(s);
          return s.toString()+' MB';
        }

      },
    },
    mounted: function() {
      // this.restaurants = this.loadAll();
      this.loadAllData();
      

    },

  };
</script>

<style>
  .el-table .warning-row {
    background: #F8F8FF;
  }
  .el-table .success-row {
    background: #F0FFFF;
  }

  
</style>


