<template> 
  <div class="headbar" :style="{'background':themeColor}" :class="collapse?'position-collapse-left':'position-left'">
    <!-- 导航收缩 -->
    <span class="hamburg">
      <el-menu class="el-menu-demo" :background-color="themeColor" text-color="#fff" :active-text-color="themeColor" mode="horizontal">
        <el-menu-item index="1" @click="onCollapse">
          <hamburger :isActive="collapse"></hamburger>
        </el-menu-item>
      </el-menu>
    </span>


    <!-- 工具栏 -->
    <span class="toolbar">
      <el-menu class="el-menu-demo" :background-color="themeColor" text-color="#14889A" :active-text-color="themeColor" mode="horizontal">
        
        <el-menu-item index="1">
          <!-- 主题切换 -->
          <ThemePciker class="theme-picker" :default="themeColor" @onThemeChange="onThemeChange">
          </ThemePciker>
        </el-menu-item>

        <!-- 语言切换 -->
        <el-menu-item index="2" v-popover:popover-lang>  
          <li style="color:#fff;" class="fa fa-language fa-lg"></li>
          <el-popover ref="popover-lang" placement="bottom-start" trigger="click" v-model="langVisible">
            <div class="lang-item" @click="changeLanguage('zh_cn')">简体中文</div>
            <div class="lang-item" @click="changeLanguage('en_us')">ENGLISH</div>
          </el-popover>
        </el-menu-item>


        <el-menu-item index="5" v-popover:popover-personal>
          <!-- 用户信息 -->
          <span class="user-info"><i  style="color:white" class="fa fa-user-o fa-1x" aria-hidden="true"></i></span>
          <el-popover ref="popover-personal" placement="bottom-end" trigger="click" :visible-arrow="false">
            <PersonalPanel :username="username"></PersonalPanel>
          </el-popover>
        </el-menu-item>

      </el-menu>
    </span>
  </div>
</template>

<script>
// import mock from "@/mock/index"

import { mapState } from 'vuex'
import Hamburger from "@/components/Hamburger"
import ThemePciker from "@/components/ThemePicker"
import PersonalPanel from "@/views/Core/PersonalPanel"

export default {
  components: {
    Hamburger,
    ThemePciker,
    PersonalPanel
  },
  data() {
    return {
      username: '',
      activeIndex: '1',
      langVisible: false
    }
  },
  methods: {
    selectNavBar(key, keyPath) {
      // this.$api.output.log(key, keyPath)
    },
    // 折叠导航栏
    onCollapse: function() {
      this.$store.commit('onCollapse')
    },
    onThemeChange: function(themeColor) {
      this.$store.commit('setThemeColor', themeColor)
    },
    changeLanguage(lang) {
      lang === '' ? 'zh_cn' : lang
      this.$i18n.locale = lang
      this.langVisible = false
      this.$api.output.log('current lang = ' + this.$i18n.locale)
    }
  },
  mounted() {
    try{
      this.username = this.$store.state.loginInfo.loginInfo.data.username;
    } catch(e) {
      return;
    }  
  },
  computed: {
    ...mapState({
      themeColor: state=>state.app.themeColor,
      collapse: state=>state.app.collapse
    })
  },

}
</script>

<style scoped lang="scss">
.headbar {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1030;
  height: 60px;
  line-height: 60px;
  border-color: rgba(180, 190, 190, 0.8);
  border-left-width: 1px;
  border-left-style: solid;
}
.hamburg {
  float: left;
}
.navbar {
  float: left;
}
.toolbar {
  float: right;
}
.lang-item {
  font-size: 16px;
  padding-left: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  cursor: pointer;
}
.lang-item:hover {
  font-size: 18px;
  background: #b0d6ce4d;
}
.user-info {
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
    margin: 10px 0px 10px 10px;
    float: right;
  }
}
.badge {
  line-height: 18px;
}
.position-left {
  left: 200px;
}
.position-collapse-left {
  left: 65px;
}
</style>


