<template>
    <div class="container">
        <!-- 导航菜单栏(头-左) NavBar.vue -->
        <nav-bar></nav-bar>
        <!-- 头部区域（头-右） HeadBar.vue -->
        <head-bar></head-bar>
        <!-- 主内容区域  MainContent.vue-->
        <main-content></main-content>
    </div>
</template>

<script>
import HeadBar from "./HeadBar"
import NavBar from "./NavBar"
import MainContent from "./MainContent"
import Cookies from "js-cookie"
export default {
    mounted: function(){
        let loginInfo = this.$store.state.loginInfo.loginInfo;
        this.$api.output.log('Home.vue - mounted: ', loginInfo);
        if (loginInfo === {}) {
            this.$router.push('/login');
            return;
        }

        if (loginInfo.status !== true) {
            this.$router.push('/login');
            return;
        }
    },
    components: {
        HeadBar,
        NavBar,
        MainContent
    },
}
</script>

<style scoped lang="scss">
  .container {
    position:absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    // background: rgba(224, 234, 235, 0.1);
  }
</style>


