<template>
	<div class="menu-bar-container">
    <!-- logo -->
    <div class="logo" :style="{'background':themeColor}" :class="collapse?'menu-bar-collapse-width':'menu-bar-width'"
        @click="$router.push('/')">
        <div v-if="collapse"><i style="color:white" class="fa fa-ravelry fa-1x" aria-hidden="true" /></div>
        <!-- <div>{{collapse?'':appName}}</div> -->
        <div><i class="fa fa-ravelry" aria-hidden="true"></i>&nbsp;{{collapse?'':$t('appName')}}</div>
        <!-- <img v-if="collapse"  src="@/assets/logo.png"/> <div>{{collapse?'':appName}}</div> -->
    </div>

 
    <!-- 导航菜单 -->
    <el-menu ref="navmenu" default-active="editableTabsValue" :class="collapse?'menu-bar-collapse-width':'menu-bar-width'"
      :collapse="collapse" :collapse-transition="false" :unique-opened="true"
      @open="handleopen" @close="handleclose" @select="handleselect" >

      <!-- 导航菜单树组件，动态加载菜单 -->
      <template v-for="item in navMenus">
        <el-submenu v-if="item.type==='submenu'" :index="item.name" :key="item.name" :menu="item">
          <template slot="title">
          <i :class="item.icon" aria-hidden="true"></i>  
          <span slot="title">{{$t(item.title)}}</span>
          </template>
          <el-menu-item v-for="child in item.children" :key="child.name" :menu="child" :index="child.name">
            <i :class="child.icon" aria-hidden="true"></i>  
            <span slot="title">{{$t(child.title)}}</span>
          </el-menu-item>
        </el-submenu>

        <el-menu-item  v-else :index="item.name" :key="item.name" :menu="item">
          <i :class="item.icon" aria-hidden="true"></i>  
          <span slot="title">{{$t(item.title)}}</span>
        </el-menu-item>
      
      </template>

      </el-menu>




	</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      appName: state=>state.app.appName,
      themeColor: state=>state.app.themeColor,
      collapse: state=>state.app.collapse,
      navMenus: state=>state.navMenu.navMenus,
      editableTabsValue: state=>state.tab.editableTabsValue,
    }),
  },
  mounted: function() {
    // console.log('navMenus = ', this.navMenus);
    // console.log('appName = ', this.appName);
  },
  methods: {
    findTab(selectTab){
      let a = selectTab;
      let tab = '';
      for(let i = 0; i < this.navMenus.length; i++) {
          if(this.navMenus[i].type === 'menu' && this.navMenus[i].name === a){
              tab = this.navMenus[i];
              return tab;
          }

          if(this.navMenus[i].type === 'submenu') {
            for(let j = 0; j < this.navMenus[i].children.length; j++) {
              if(this.navMenus[i].children[j].type === 'menu' && this.navMenus[i].children[j].name === a){
                tab = this.navMenus[i].children[j];
                return tab;
              }
            }
          }
      }
      return ''
    },
    handleopen() {
      
    },
    handleclose() {
      
    },
    handleselect(a, b) {
      let tab = this.findTab(a);
      this.$store.commit('pushEditableTabs', {
        title: tab.title, name: tab.name, content: tab.title 
      });  
      this.$store.commit('setEditableTabsValue', tab.name);  
    }
  }
}
</script>

<style scoped lang="scss">
.menu-bar-container {
  position: fixed;
  top: 0px;
  left: 0;
  bottom: 0;
  z-index: 1020;
  .el-menu {
    position:absolute;
    top: 60px;
    bottom: 0px;
    text-align: left;
    // background-color: #2968a30c;
  }
  .logo {
    position:absolute;
    top: 0px;
    height: 60px;   
    line-height: 60px;
    background: #545c64;
    cursor:pointer;
    img {
        width: 40px;
        height: 40px;
        border-radius: 0px;
        margin: 10px 10px 10px 10px;
        float: left;
    }
    div {
      font-size: 25px;
      color: white;
      text-align: left;
      padding-left: 20px;
    }
  }
  .menu-bar-width {
    width: 200px;
  }
  .menu-bar-collapse-width {
    width: 65px;
  }
}

</style>


