import axiosClient from '../axios-client'

// 查找用户的菜单权限标识集合
export const allUsers = (params) => {
    return axiosClient({
        url: '/pointUser',
        method: 'get',
        params
    })
}

export const loginHost = (params) => {
    return axiosClient({
        url: '/login',
        method: 'post',
        data: params
    })
}


