<template>
  <div class="page-container" style="background-color: white" :onloaded="onloaded()">
	<!-- top tool bar -->
	<!-- <div class="toolbar" style="float:left;padding-top:10px;padding-left:15px;" >
	</div> -->

  <div style="padding-top:10px;padding-left:15px;">
    
  <!-- 分割线 -->
  <!-- <el-divider></el-divider> -->

  <el-table ref="multipleTable" :data="pageLists" tooltip-effect="dark" style="width: 100%" :row-style="rowStyle" :cell-style="cellStyle" @selection-change="handleSelectionChange" :row-class-name="tableRowClassName">
    <el-table-column :label="$t('panel.panelAllFiles.base.type')" width="120" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-button  v-if="tableData[scope.row.index].type === 'file'" type="text" size="small">
            <i class="fa fa-file-o fa-2x" aria-hidden="true"></i>
          </el-button>

        </template>
    </el-table-column>
 
    <el-table-column prop="name" :label="$t('panel.panelAllFiles.base.name')" width="200" show-overflow-tooltip></el-table-column>
    <el-table-column prop="size" :label="$t('panel.panelAllFiles.base.size')" width="120" show-overflow-tooltip></el-table-column>
    <el-table-column prop="mTime" :label="$t('panel.panelAllFiles.base.mTime')" width="120" show-overflow-tooltip></el-table-column>
    <el-table-column prop="path" :label="$t('panel.panelAllFiles.base.path')" show-overflow-tooltip></el-table-column>
<el-table-column
      fixed="right"
      :label="$t('panel.panelAllFiles.base.operation')"
      width="200">
      <template slot-scope="scope">

        <!-- share -->
        <el-button v-if="tableData[scope.row.index].type === 'file'" type="text" size="mini"  @click="handleShareDeal(scope.row.index, tableData)">
            <i class="fa fa-share-alt fa-2x" aria-hidden="true"></i>
        </el-button>

        <!-- delete -->
        <el-button type="text" size="mini"  @click="handleDeleteDeal(scope.row.index, tableData)">
            <i class="fa fa-trash-o fa-2x" aria-hidden="true"></i>
        </el-button>
      
      </template>
    </el-table-column>
  </el-table>

  <p></p>
  <el-divider></el-divider>
  <p></p>
  <div style="float:left">
    <el-pagination background layout="prev, pager, next"
    @current-change="handleCurrentChange"
    :current-page="currentPage"
    :page-size="10"
    :total="listsDataNumber" 
    ></el-pagination>
  </div>
  </div>


  </div>

</template>

<script>
import axios from 'axios';
import Vue from 'vue'

export default {
    components: {
    },
    data() {
      return {
        loading: null,
        restaurants: [],
        state: '',
        timeout:  null,
        tableData: null,
        multipleSelection: [],

        currentPage: 1,
        listsDataNumber: 0,
        pageLists: [],

        dialogRenameFormVisible: false,
        tableDataItem: {
          index: '',
          name: '',
          type: '',
          size: '',
          mTime: '',
          path:''
        },
        
        dialogMoveFormVisible: false,
        dialogCopyFormVisible: false,
        dialogFileUploadFormVisible: false,
        dialogMoreFormVisible: false,
        location: Vue.prototype.baseConfig.baseUrl
      };
    },
    methods: {
      // download
      handleFileDownloadDeal: async function(index, rows) {
        let path = rows[index].path;
        let loginInfo = this.$store.state.loginInfo.loginInfo;
        let data = {
            hostIp: loginInfo.data.hostIp,
            username: loginInfo.data.username,
            remotePath: path
        };
        this.$api.optFiles.downloadFile(data, rows[index].name);
      },
      showDialogMoveDealEx: function(index, rows){
        this.tableDataItem.index = index;
        this.tableDataItem.path = rows[index].path;
        this.tableDataItem.name = rows[index].name;
        this.dialogMoveFormVisible = true;
      },
      showDialogMoveDeal: function(flag){
        this.dialogMoveFormVisible = flag;
      },
      showDialogRenameDealEx: function(index, rows){
        this.$api.output.log('showDialogRenameDealEx = ', rows[index]);
        this.tableDataItem.index = index;
        this.tableDataItem.date = rows[index].date;
        this.tableDataItem.name = rows[index].name;
        this.tableDataItem.oldName = rows[index].name;
        this.tableDataItem.path = rows[index].path;
        this.showDialogRenameDeal(true);
      },
      showDialogRenameDeal: function(flag){
        this.dialogRenameFormVisible = flag;
      },
      showDialogCopyDealEx: function(index, rows){
        this.tableDataItem.index = index;
        this.tableDataItem.path = rows[index].path;
        this.tableDataItem.name = rows[index].name;
        this.showDialogCopyDeal(true);
      },
      showDialogCopyDeal: function(flag){
        this.dialogCopyFormVisible = flag;
      },
      handleShareDeal: function(index, rows){
        let path = this.location+rows[index].path;
        this.$alert('<center><a href="'+path+'" target="_blank">'+path+'</a></center>', this.$t('panel.panelShareFiles.share'), {
          dangerouslyUseHTMLString: true,
          confirmButtonText: this.$t('common.confirm')
        });

      },
      handleDeleteDeal: function(index, rows){
        this.$confirm(this.$t('panel.panelAllFiles.base.deleteTip'), this.$t('common.tip'), {confirmButtonText: this.$t('common.confirm'), cancelButtonText: this.$t('common.cancel'), type: 'warning'})
        .then(()=>{
            
            let loginInfo = this.$store.state.loginInfo.loginInfo;
      
            let args = {
                hostIp: loginInfo.data.hostIp,
                username: loginInfo.data.username,
                path: '.'+rows[index].path,
            };

            console.log('PanelShareFiles.vue:', args);

            this.$api.optShareFiles.removeShareFile(args).then(()=>{
                
                var sleepDeal = function(time) {
                var startTime = new Date().getTime() + parseInt(time, 10);
                    while(new Date().getTime() < startTime) {}
                };
                sleepDeal(1000); // 延时函数，单位ms
                this.flushTableData();
                
            }).catch(()=>{});

        })
        .catch(()=>{

        });

      },

      ///////////////////////////////////////////////////////////
      // main table deal
      rowStyle({row, column, rowIndex, columnIndex}){
        return "height:0";
      },
      cellStyle({row, column, rowIndex, columnIndex}){
        return "padding:0";
      },
      loadAllData: async function(){
        this.load();
        let ret = await this.$api.optShareFiles.getShareFiles({});
        this.tableData = ret['data'];
        for(let i = 0; i < this.tableData.length; i++){
          this.tableData[i].index = i; 
          this.tableData[i].size = this.sizeDeal(this.tableData[i].size);
        }
        this.listsDataNumber = this.tableData.length;
        this.initPageLists()
      },
      load: function() {
        this.loading = this.$loading({  
          lock: true,
          text: "loading...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.5)",
          fullscreen: false,
          target: document.querySelector("#main-container ")
        })
      },
      onloaded: function() {
        // console.log('onload ...');
        if(this.loading) {
          this.loading.close();
        } 
      },
      tableRowClassName: function({row, rowIndex}) {
        if (rowIndex % 4 === 0) {
          return 'warning-row';
        } 
        else if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },
      handleSelectionChange: function(val) {
        this.multipleSelection = val;
      },
      handleCurrentChange(val) {
          this.pageLists = this.tableData.slice((val-1)*10,val*10)
      },
      initPageLists(){
          this.pageLists = this.tableData.slice(0,10)
      },

      flushTableData: async function() {
          let ret = await this.$api.optShareFiles.getShareFiles({});
          this.tableData = ret['data'];
          for(let i = 0; i < this.tableData.length; i++){
            this.tableData[i].index = i; 
            this.tableData[i].size = this.sizeDeal(this.tableData[i].size);
          }

          this.listsDataNumber = this.tableData.length;
          this.initPageLists()
      },

      // file size unit trans
      sizeDeal: function(size) {
        let bLen = size.toString().length;
        if (bLen < 4){
           return size.toString()+' B';
        }

        if (bLen >= 4 && bLen <= 7){
          let s = size/1024;
          s = Math.round(s);
          return s.toString()+' KB';
        }

        if (bLen >= 8){
          let s = size/1024/1024;
          s = Math.round(s);
          return s.toString()+' MB';
        }

      },
    },
    mounted: function() {
      // this.restaurants = this.loadAll();
      this.loadAllData();
      

    },

  };
</script>

<style>
  .el-table .warning-row {
    background: #F8F8FF;
  }
  .el-table .success-row {
    background: #F0FFFF;
  }
</style>


