// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'font-awesome/css/font-awesome.min.css'
import axios from 'axios'

import i18n from './i18n'
import store from './store'
import api from './http'

Vue.config.productionTip = false

// 注册使用 element
Vue.use(ElementUI)

// 注册使用API模块
Vue.use(api)  

// 挂载全局配置
Vue.prototype.global = global

/* eslint-disable no-new */
axios.get('./static/project.config.json').then((result) => {
  Vue.prototype.baseConfig = result.data;

  new Vue({
    el: '#app',
    i18n,
    router,
    store,
    components: { App },
    template: '<App/>'
  })

  // 初始化
  store.state.navMenu.initTab = Vue.prototype.baseConfig.nav.initTab;
  store.state.navMenu.navMenus = Vue.prototype.baseConfig.nav.navMenus;
  store.state.tab.editableTabsValue = store.state.navMenu.initTab.editableTabsValue;
  store.state.tab.editableTabs = store.state.navMenu.initTab.editableTabs;

  console.log('main.js - store.state.navMenu = ', store.state.navMenu);

}).catch((error) => {
     console.log('get baseConfig error...' + error)
})


