import axiosClient from '../axios-client'


export const tableTest = (params) => {
    return axiosClient({
        url: '/tableTest',
        method: 'post',
        params
    })
}

export const autoComplete = (params) => {
    return axiosClient({
        url: '/autoComplete',
        method: 'post',
        params
    })
}


