<template>
  <div id="main-container" class="main-container" :class="$store.state.app.collapse?'position-collapse-left':'position-left'">
    <!-- <p>{{navTab}}</p> -->

    <!-- 标签页 -->
    <div class="tab-container">
      <!-- <el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab" @tab-click="selectedTabHandle"> -->
      <el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab" @tab-click="selectedTabHandle"> 
        <el-tab-pane
          v-for="(item) in editableTabs"
          :key="item.name"
          :label="$t(item.title)"
          :name="item.name">

   
      <!-- <keep-alive class="main-content">
        <transition name="fade" mode="out-in">
            <router-view></router-view>
        </transition>
      </keep-alive> -->
 

        </el-tab-pane>
      </el-tabs>
    </div>

    <!-- 主内容区域 -->
    <div class="main-content">
      <keep-alive>
        <transition name="fade" mode="out-in">
            <router-view></router-view>
        </transition>
      </keep-alive>
    </div>
 
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  computed: {
    ...mapState({
      // editableTabsValue: state=>state.tab.editableTabsValue, 
      // editableTabs: state=>state.tab.editableTabs,
    }),
    editableTabs: {
        get(){
          // this.$api.output.log('MainContent.vue - editableTabsValue = ', this.$store.state.tab.editableTabsValue);
          if(this.$store.state.tab.editableTabsValue != null) {
              this.$router.push(this.$store.state.tab.editableTabsValue);
          }
          return this.$store.state.tab.editableTabs;
        }
    },
    editableTabsValue: {
        get(){
          let tab = this.$store.state.tab.editableTabsValue;
          return tab;
          },
        set(val){this.$store.state.tab.editableTabsValue = val;}
    },
  },
  methods: {
      selectedTabHandle (tab) {
        // this.$api.output.log(tab.name);
        this.$router.push(tab.name);
      },
      removeTab(tab) {
        // this.$api.output.log('removeTab = ', tab);
        this.$store.commit('popEditableTabs', tab);  
      },
  },
  
}
</script>

<style scoped lang="scss">

.main-container {
  // padding: 0 5px 5px;
  position: absolute;
  top: 60px;
  left: 1px;
  right: 1px;
  bottom: 0px;
  // background: rgba(67, 69, 70, 0.1);
    .main-content {
      position: absolute;
      top: 45px;
      left: 5px;
      right: 5px;
      bottom: 5px;
      padding: 5px;
    }
}
.position-left {
  left: 200px;
}
.position-collapse-left {
  left: 65px;
}
</style>


