<template>
  <!-- Form -->
  <el-dialog :title="$t('panel.panelAllFiles.dialogRenameDeal.rename')" :visible.sync="dialogRenameFormVisible" :showClose="false" :beforeClose="closeDialog" :width="'30%'">
    <el-form>
      <el-form-item  :label="$t('panel.panelAllFiles.dialogRenameDeal.index')">
        <el-input v-model="tableDataItem.index" autocomplete="off" :disabled="true"></el-input>
      </el-form-item>

      <el-form-item  :label="$t('panel.panelAllFiles.dialogRenameDeal.name')">
        <el-input v-model="tableDataItem.name" autocomplete="off"></el-input>
      </el-form-item>

    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="pointEditOver(false)">{{$t('common.cancel')}}</el-button>
      <el-button type="primary" @click="pointEditOver(true)">{{$t('common.confirm')}}</el-button>
    </div>

  </el-dialog>
</template>

<script>
export default {
    name: 'DialogRenameDeal',
    props: {
        dialogRenameFormVisible: {
            type: Boolean,
            required: true,
        },
        tableDataItem: {
            type: Object,
            required: true,
        },
        // tableData: {
        //     type: Array,
        //     required: true,
        // },
        location: {
            type: String,
            required: true,           
        }
    },
    data(){
        return {
            
        };
    },
    methods: {
        closeDialog: function(){
            this.$emit('showDialogRenameDeal', false);
            this.$emit('flushTableData', this.location);
        },
        pointEditOver: async function(flag){
            if(flag){

            let pos = this.tableDataItem.index;
            let loginInfo = this.$store.state.loginInfo.loginInfo;

            let ppos = this.tableDataItem.path.lastIndexOf('/');
            let path = '/';
            if (ppos !== 0) {
                path = this.tableDataItem.path.substring(0, ppos);
                path += '/'
            }

            let argRename = {
                hostIp: loginInfo.data.hostIp,
                username: loginInfo.data.username,
                oldPath: path+this.tableDataItem.oldName,
                newPath: path+this.tableDataItem.name
            };

            let ret = await this.$api.optFiles.renameFile(argRename);
            // this.tableData[pos].name = this.tableDataItem.name;
            this.$emit('flushTableData', this.location);
            }
            // this.dialogFormVisible = false;
            this.$emit('showDialogRenameDeal', false);
            // console.log('PanelAllFiles.vue - pointEditOver', this.tableData[pos].name);
        },
    }
}
</script>

<style>

</style>



