<template>

  <el-form :model="loginForm" :rules="getFieldRules()" ref="loginForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <span class="tool-bar">
    </span>
    <ThemeRender  class="theme-picker" :default="themeColor" @onThemeChange="onThemeChange"></ThemeRender>
    <!-- login tip -->
    <h2 class="title" style="padding-left:0px;" ><i class="fa fa-ravelry" aria-hidden="true"></i>&nbsp;{{$t('appName')}}</h2>
    

    <el-form-item prop="hostIp">
      <el-input type="text" v-model="loginForm.hostIp" auto-complete="off" :placeholder="$t('base.login.hostIp')"></el-input>
    </el-form-item>
    <el-form-item prop="username">
      <el-input type="text" v-model="loginForm.username" auto-complete="off" :placeholder="$t('base.login.username')"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="loginForm.password" auto-complete="off" :placeholder="$t('base.login.password')"></el-input>
    </el-form-item>


    <el-form-item style="width:100%;">
       <el-button  style="width:8%;" type="text" v-popover:popover-lang>  
          <li class="fa fa-language fa-lg"></li>
          <el-popover ref="popover-lang" placement="bottom-start" trigger="click" v-model="langVisible">
            <div class="lang-item" @click="changeLanguage('zh_cn')">简体中文</div>
            <div class="lang-item" @click="changeLanguage('en_us')">ENGLISH</div>
          </el-popover>
       </el-button>

      <el-button style="width:42%;" type="primary" @click.native.prevent="reset">{{$t('common.reset')}}</el-button>
      <el-button style="width:42%;" type="primary" @click.native.prevent="login" :loading="loading">{{$t('common.login')}}</el-button>
    </el-form-item>
  </el-form>


</template>

<script>
import Cookies from "js-cookie"
import { mapState } from 'vuex'
import ThemeRender from "./Core/ThemeRender"
export default {
  name: 'Login',
  components: {
    ThemeRender,
  },
  data() {
    return {
      langVisible: false,
      loading: false,
      loginForm: { 
        hostIp: '',
        username: '',
        password: '',
        src: ''
      },
      fieldRules: {
        hostIp: [
          { required: true, message: this.$t('base.login.hostIpTip'), trigger: 'blur' }
        ],
        username: [
          { required: true, message: this.$t('base.login.usernameTip'), trigger: 'blur' }
        ],
        password: [
          { required: true, message: this.$t('base.login.passwordTip'), trigger: 'blur' }
        ]
      },
      checked: true
    }
  },
  methods: {
    onThemeChange: function() {
    },
    getFieldRules() {

      let fieldRules = {
        hostIp: [
          { required: true, message: this.$t('base.login.hostIpTip'), trigger: 'blur' }
        ],
        username: [
          { required: true, message: this.$t('base.login.usernameTip'), trigger: 'blur' }
        ],
        password: [
          { required: true, message: this.$t('base.login.passwordTip'), trigger: 'blur' }
        ]
      };
      return fieldRules;
    },
    changeLanguage(lang) {
      lang === '' ? 'zh_cn' : lang
      this.$i18n.locale = lang
      this.langVisible = false
      this.$api.output.log('current lang = ' + this.$i18n.locale)
    },

    async login() {
      this.loading = true
      let userInfo = {
            hostIp:this.loginForm.hostIp, 
            username:this.loginForm.username, 
            password:this.loginForm.password, 
      }

      try {
        this.$api.output.log('Login.Vue - userInfo: ', userInfo);
        let ret = await this.$api.user.loginHost(userInfo);
        this.$api.output.log('Login.Vue - ret: ', ret);
        if (ret.status === true) {
          this.$store.commit('setLoginInfo', ret);
          this.$router.push('/');  // 登录成功，跳转到主页
        } else {
          this.$message.error(this.$t('base.login.inputTip1')); 
        }
      } catch(err) {
        this.$message.error(this.$t('base.login.inputTip2')+(err.message));
      } finally {
        this.loading = false;
      }

    },
    reset() {
      this.$api.output.log('reset')
      this.loginForm.username = ''
      this.loginForm.password = ''
    },
  },
  mounted() {
    // console.log('themeColor:', this.themeColor);
    this.$store.commit('setThemeColor', this.themeColor)
  },
  computed: {
    ...mapState({
      themeColor: state=>state.app.themeColor
    })
  },
}
</script>

<style lang="scss" scoped>
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 100px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 30px auto;
      text-align: center;
      color: #505458;
    }
  }


.headbar {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1030;
  height: 60px;
  line-height: 60px;
  border-color: rgba(180, 190, 190, 0.8);
  border-left-width: 1px;
  border-left-style: solid;
}
.hamburg {
  float: left;
}
.navbar {
  float: left;
}
.toolbar {
  float: right;
}
.lang-item {
  font-size: 16px;
  padding-left: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  cursor: pointer;
}
.lang-item:hover {
  font-size: 18px;
  background: #b0d6ce4d;
}
.user-info {
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
    margin: 10px 0px 10px 10px;
    float: right;
  }
}
.badge {
  line-height: 18px;
}
.position-left {
  left: 200px;
}
.position-collapse-left {
  left: 65px;
}

.theme-picker .el-color-picker__trigger {
  vertical-align: middle;
}

.theme-picker-dropdown .el-color-dropdown__link-btn {
  display: none;
}

</style>


