export default {
    state: {
        appName: "NetShell",  // 应用名称
        themeColor: "#191970",  // 主题颜色
        oldThemeColor: "#191970",   // 上一次主题颜色
        collapse: false,  // 导航栏收缩状态
        menuRouteLoaded: false,    // 菜单和路由是否已经加载

        currentTab: "test1",
        currentTabs: [{
            title: 'test1',
            name: 'test1',
            content: 'Tab 1 content'
        },],

        currentInitTab: "test1",
        currentInitTabs: [{
            title: 'test1',
            name: 'test1',
            content: 'Tab 1 content'
        },],

    },
    getters: {
        collapse(state){// 对应着上面state
            return state.collapse
        }
    },
    mutations: {
        onCollapse(state){  // 改变收缩状态
            state.collapse = !state.collapse
        },
        setThemeColor(state, themeColor){  // 改变主题颜色
            state.oldThemeColor = state.themeColor
            state.themeColor = themeColor
        },
        menuRouteLoaded(state, menuRouteLoaded){  // 改变菜单和路由的加载状态
            state.menuRouteLoaded = menuRouteLoaded;
        },
        initCurrentTabs(state) {
            console.log('state.currentInitTabs = ', state.currentInitTabs)
            state.currentInitTab = state.currentInitTab;
            state.currentInitTabs = state.currentInitTabs;
        }
    },
    actions: {
    }
}


