import axiosClient from '../axios-client'

// 
export const getShareFiles = (params) => {
    return axiosClient({
        url: '/getShareFiles',
        method: 'post',
        data: params
    })
}


export const removeShareFile = (params) => {
    return axiosClient({
        url: '/removeShareFile',
        method: 'post',
        data: params
    })
}


