import Vue from 'vue'
import vuex from 'vuex'

Vue.use(vuex);

// 引入子模块
import app from './modules/app'
import navMenu from './modules/nav-menu'
import tab from './modules/tab'
import loginInfo from './modules/login-info'

const store = new vuex.Store({
    modules: {
        app: app,
        navMenu: navMenu,
        tab: tab,
        loginInfo: loginInfo
        
    }
})

export default store;


