<template>
  <!-- 文件上传对话框 -->
  <el-dialog title="" :visible.sync="dialogFileUploadFormVisible" :showClose="false" :beforeClose="closeDialog" :width="'30%'">
    <el-form>
      <el-upload
        class="upload-demo"
        ref="upload"
        :action="uploadfileUrl"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        :beforeUpload="beforeAvatarUpload"
        :headers="uploadHeaders"
        :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">{{$t('panel.panelAllFiles.dialogFileUploadDeal.selectFile')}}</el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">{{$t('panel.panelAllFiles.dialogFileUploadDeal.uploadFileToServer')}}</el-button>
        <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
      </el-upload>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <!-- <el-button @click="UploadFileDialogCancel(false)">取 消</el-button> -->
      <el-button type="primary" @click="UploadFileDialogConfirm(true)">{{$t('panel.panelAllFiles.dialogFileUploadDeal.finishedProcessing')}}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import Vue from 'vue'
export default {
    name: 'DialogFileUploadDeal',
    props: {
        dialogFileUploadFormVisible: {
            type: Boolean,
            required: true,
        },
        location: {
            type: String,
            required: true,           
        }
    },
    data(){ 
        return {
            fileList: [],
            uploadfileUrl: Vue.prototype.baseConfig.baseUrl+'/uploadfile',
            uploadHeaders: {
            'upload-params': '',
            'file-size': 0
            },
        }
    },
    methods: {
        closeDialog: function(){
            this.$api.output.log('DialogFileUploadDeal.vue - closeDialog');
            this.$emit('showUploadFileDialog', false);
            this.$emit('flushTableData', this.location);
        },
        UploadFileDialogConfirm: async function() {
          

        //   let path = this.location;
          // console.log('PanelAllFiles.vue - pointGoParent1',this.location, pos, path);

        //   let loginInfo = this.$store.state.loginInfo.loginInfo;
        //   let argListFiles = {
        //     hostIp: loginInfo.data.hostIp,
        //     username: loginInfo.data.username,
        //     location: path
        //   };

        //   let ret = await this.$api.optFiles.listFiles(argListFiles);
        //   // console.log('PanelAllFiles.vue - pointGoParent2', ret);
        //   this.tableData = ret['data'];
        //   for(let i = 0; i < this.tableData.length; i++){
        //     this.tableData[i].index = i; 
        //     this.tableData[i].size = this.sizeDeal(this.tableData[i].size);
        //   }

        //   this.listsDataNumber = this.tableData.length;
        //   this.initPageLists()
        this.$emit('flushTableData', this.location);
        this.$emit('showUploadFileDialog', false);
      },
      UploadFileDialogCancel: function() {
          this.$emit('showUploadFileDialog', false);
      },
      beforeAvatarUpload(file) {
        this.uploadHeaders['file-size'] = file.size;
      },
      submitUpload() {
        let loginInfo = this.$store.state.loginInfo.loginInfo;
        let params = {
          hostIp: loginInfo.data.hostIp,
          username: loginInfo.data.username,
          location: this.location
        };

        this.uploadHeaders['upload-params'] = JSON.stringify(params);
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
        this.$api.output.log(file, fileList);
      },
      handlePreview(file) {
        this.$api.output.log(file);
      },
    }
}
</script>

<style>

</style>



